* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
}

html {
  height: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

::-webkit-input-placeholder,
input::-webkit-input-placeholder {
  color: #000000 !important;
}

::-moz-placeholder,
input::-webkit-input-placeholder {
  color: #000000 !important;
}

:-ms-input-placeholder,
input:-ms-input-placeholder {
  color: #000000 !important;
}

:-moz-placeholder,
input:-moz-placeholder {
  color: #000000 !important;
}

/* Let's get this party started */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 8px;
  background-color: #000000;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.1);
}
::-webkit-scrollbar-thumb:window-inactive {
  background-color: #000000;
}

body.body {
  font-family: "Poppins-Regular";
  font-weight: 500;
  min-height: 100%;
  color: #000000;
  display: flex;
  line-height: 1.5;
  font-size: 16px;
  overflow: hidden auto;
}
body.body .h2,
body.body h2 {
  font-size: 32px;
  font-family: "Mont-HeavyDEMO";
}
body.body a {
  transition: all 0.3s linear;
  text-decoration: none;
}
body.body a:hover, body.body a:focus {
  outline: none !important;
  box-shadow: none;
  text-decoration: none;
}
body.body .btn {
  padding: 8px 40px;
  min-width: 160px;
  border: 1px solid transparent;
  transition: all 0.3s linear;
  border-radius: 0;
  text-transform: capitalize;
  font-family: "Poppins-Medium";
  border-radius: 25px;
}
body.body .btn:focus {
  box-shadow: none;
}
body.body .btn.btn-light {
  background-color: #233B4B;
  border-color: #233B4B;
  color: #ffffff;
}
body.body .btn.btn-light:hover, body.body .btn.btn-light:focus {
  background-color: white;
  color: #233B4B;
}
body.body .btn.btn-default {
  background-color: #233B4B;
  border-color: #233B4B;
  color: #ffffff;
}
body.body .btn.btn-default:hover, body.body .btn.btn-default:focus {
  background-color: transparent;
  color: #233B4B;
}
body.body .btn.btn-info {
  background-color: #20A3D5;
  border-color: #20A3D5;
  color: #ffffff;
}
body.body .btn.btn-info:hover, body.body .btn.btn-info:focus {
  background-color: white;
  color: #20A3D5;
}
body.body .btn.btn-orange {
  background-color: #FF7243;
  border-color: #FF7243;
  color: #ffffff;
}
body.body .btn.btn-orange:hover, body.body .btn.btn-orange:focus {
  background-color: white;
  color: #FF7243;
}
body.body p {
  font-family: "Poppins-Regular";
}

main {
  position: relative;
  flex: 1 0 auto;
}

.padR-0 {
  padding-right: 0;
}

.padL-0 {
  padding-left: 0;
}

.pad-0 {
  padding: 0;
}

.pad-15 {
  padding: 15px;
}

.pd-50 {
  padding: 50px 0;
}

.page-wrapper {
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}

.main {
  z-index: 2;
  position: relative;
}

